<template>
    <div id="top">
        <van-notice-bar
            text="请仔细填写信息，如果由于您填写错误导致资金流失，平台概不负责。"
            background="#F4F4F4"
            color="#F5A623"
            :scrollable="false"
            :wrapable="true"
            left-icon="info"/>

        <div id="top1">
            <span>选择提现方式</span>
            <div @click="index=0" :class="{active:index==0}">
                <img src="@/assets/images/withdrawDeposit1.png" alt="">
                <span>支付宝</span>
            </div>
            <!-- <div @click="index=1;qiehuan()" :class="{active:index==1}">
                <img src="@/assets/images/withdrawDeposit2.png" alt="">
                <span>微信</span>
            </div> -->
            <!-- <div @click="index=3;qiehuan()" :class="{active:index==3}">
                <img src="@/assets/images/withdrawDeposit3.png" alt="">
                <span>银行卡</span>
            </div> -->
        </div>

        <div style="height:60pt;">


            <div v-show="index==0" class="height">
                <div class="bottom">
                    <span>支付宝账户</span>
                    <input type="text" placeholder="请输入支付宝账户" v-model="u_id">
                </div>
                <div class="bottom">
                    <span>收款人姓名</span>
                    <input type="text" placeholder="请输入收款人真实姓名" v-model="u_name">
                </div>
            </div>


            <div v-show="index==1" class="height">
                <div class="bottom">
                    <span>微信账户</span>
                    <input type="text" placeholder="请输入微信账户" v-model="u_id">
                </div>
                <div class="bottom">
                    <span>收款人姓名</span>
                    <input type="text" placeholder="请输入收款人真实姓名" v-model="u_name">
                </div>
            </div>


            <div v-show="index==3" class="height">
                <div class="bottom">
                    <span>银行卡账户</span>
                    <input type="text" placeholder="请输入银行卡账户" v-model="u_id">
                </div>
                <div class="bottom">
                    <span>收款人姓名</span>
                    <input type="text" placeholder="请输入收款人真实姓名" v-model="u_name">
                </div>
            </div>
            <!-- <div id="top2" v-show="index==3&&card_info.bankname" @click="cardShow=true">
                <span class="yinhang">中国{{card_info.bankname}}</span>
                <span style="margin:10px 30px;">{{card_info.bank_cash}}</span>
            </div>
            <div id="top2" v-show="index==3&&!card_info.bankname" @click="cardShow=true">
                <span class="yinhang"></span>
                <span style="margin:10px 30px;">去绑定银行卡</span>
            </div> -->
        </div>


        <div id="top3">
            <span class="tixian">可提现金额( <i>￥{{$route.query.user_money}}</i> )</span>
            <input type="text" :placeholder="`请输入提现金额----最低提现金额为${min_cash}元`" v-model="money" @change='panduan(money)'>
        </div>

        <!-- 协议 -->
        <div class="xieyi">
          <van-radio-group v-model="value">
            <van-radio checked-color="#15BDDB" name="1">我已阅读并同意</van-radio>
          </van-radio-group>
          <span @click="isshow=true">《机大呷平台提现协议》</span>
        </div>

        <div style="text-align:center;margin-top:30px;">
            <button @click="shenqing()">申请提现 ---- 每天可提现 {{cash_times}} 次</button>
        </div>
        <van-popup style="width:90%;height:80%;border-radius:6pt;" v-model="isshow">
            <div class="xy_pop">
                <div class="top">
                    <p>第三方支付平台提现：</p>
                    <p>用户必须保证自己填写的所有资料正确有效； </p>
                    <p>第三方支付平台地址错误，第三方平台账户名错误，第三方支付平台未激活，第三方支付平台未经实名认证，我们不进行检测，将按照用户资料进行支付；一旦支付后造成的金额被退回.冻结.消失与平台无关； </p>
                    <p>对于无法支付的提现申请，我们将进行退回处理； </p>
                    <p>其它在此处未写明之不符合支付条件者，以处理支付时发送的站内信息为准。 </p>
                    <p>银行提现： </p>
                    <p>银行账号必须正确； </p>
                    <p>支行名称必须正确； </p>
                    <p>如果提现失败，需要联系客服人员核实后申请，如果不经过客服核对而申请，我们将不再处理。 </p>
                    <p>二.申明 1.所有用户只得操作自己的账户，如果非法盗取他人账户，将移送公安机关处理；</p>
                    <p>三.免责条款 </p>
                    <p>因账户保管不善造成账户被盗，造成的损失平台不承担任何责任。 </p>
                    <p>提现说明： </p>
                    <p>申请提现，在前一笔未支付成功的情况不可以提现。 </p>
                    <p>绑定第三方支付平台或银行后将不能解除绑定，最好使用自己的账号；不要借用他人账号来绑定。 </p>
                    <p>提现后1-3个工作日内到账； </p>
                    <p>提示：提现前请确认自己的收款账号正常无误；提现支付后由于收款账号问题造成的资金退回，冻结，消失，平台概不负责。</p>
                </div>
                <div class="bottom1" @click="isshow=false;value='1';">我已阅读</div>
            </div>
        </van-popup>
        <van-popup
            v-model="show1"
            title="标题"
            class="submit"
            show-cancel-button>
            <div class="submit_pop">
                <div class="top">
                    <p>确认提现金额：<strong class="price">￥{{money}} </strong> 到支付宝账号</p>
                    <div class="code">
                        <span class="span">{{u_id}}</span>
                    </div>
                </div>

                <div class="btn">
                    <div class="c9" @click="cancel()">取消</div>
                    <div @click="confirm()">确认</div>
                </div>
            </div>
        </van-popup>
    </div>
</template>
<script>
import { NavBar, Popup, Toast, NoticeBar, Radio, Dialog, RadioGroup } from 'vant'

import { getwendit_system, getwendit } from '@/axios/getData'
export default {
    data(){
        return{
            show1:false,         //  提交确认
            isshow:false,       //  协议弹出层
            value:0,            //  是否阅读过提现协议
            cardShow:false,
            index:0,
            card_info:{},       //  银行卡信息
            user_money:'',
            u_id:'',
            u_name:'',
            money:'',
            min_cash:'',    // 最低提现金额
            cash_times:''   // 每天可提现次数
        }
    },
    created(){
        // this.getcard_1();
        // this.getyu_e();
        this.user_money = this.$route.query.user_money
        this.getdata();
    },
    mounted(){
        var that = this;
        that.$hu.$on('getcard',function(val){
            that.cardShow = false;
            that.card_info = val;
        })
    },
    methods:{
        // 是否阅读过提现协议
        // ready(){
        //   if(this.value){
        //     this.value = 0;
        //     return;
        //   }  
          
        // },
        async getdata(){
            var res = await getwendit_system();
            this.min_cash = res.data.min_cash;
            this.cash_times = res.data.cash_times
        },
        // price(item){
        //     var str = item.toString();
        //     var arr = []
        //     for(let i = 0;i<str.length;i++){
        //         if(str.length<4) break;
        //         arr.push(str.substr(0,4))
        //         str = str.substr(4)
        //     }
        //     return arr
        // },
        //申请提现
        async sumbit(index){
            // if(index==1){
            //     gettixian({type:1,account:this.u_id,realname:this.u_name,money:this.money}).then((res)=>{
            //         if(res.code==200){
            //             Toast('提交成功')
            //         }else{
            //             Toast('提交失败')
            //         }
            //     })
            // } else if(index==3){
            //     gettixian({type:3,bank_id:this.card_info.id,money:this.money}).then((res)=>{
            //         if(res.code==200){
            //             Toast('提交成功')
            //         }else{
            //             Toast('提交失败')
            //         }
            //     })
            // }
        },
        qiehuan(){  //切换清空
            this.u_id='';
            this.u_name='';
            this.money='';
        },
        // 判断余额与提现金额的大小
        panduan(aa){
            console.log(Number(this.money),Number(this.$route.query.user_money))
            if(Number(this.money)<Number(this.min_cash)){
                Toast(`提现金额不能小于最低额度--${this.min_cash}元`);
                return;
            }
            if(Number(this.money)>Number(this.$route.query.user_money)){
                Toast('提现金额不能大于可提现余额')
            }
        },
        async getcard_1(){
            // var res = await bankList();
            // if(res.data.length){
            //     this.card_info = res.data[0]
            // }
            // console.log(res.data,this.card_info.bankname)
        },
        async getyu_e(){
            // var res = await chongzhi();
            // console.log(res)
            // this.user_money = res.data.user_money
        },
        /* 提交弹出层 */
        cancel(){
            this.show1 = false;
        },
        confirm(){
            // if(Number(this.money)>Number(this.user_money)){
            //     Toast('提现金额不能大于可提现余额');
            //     return;
            // }
            getwendit({werchat:this.u_id, username:this.u_name, money:this.money}).then((res)=>{
                if(res.code == 200){
                    Toast(res.data);
                    this.user_money-=this.money
                } else {

                }
            })
            this.show1 = false;
        },
        shenqing(){
            // this.show1 = true;

            if(!this.u_id||!this.u_name){
                Toast(`请输入提现账号和收款人姓名`);
                return;
            }
            if(Number(this.money)<Number(this.min_cash)){
                Toast(`提现金额不能小于最低额度--${this.min_cash}元`);
                return;
            } else if(Number(this.money)>Number(this.$route.query.user_money)){
                Toast('提现金额不能大于可提现余额');
                return;
            } else if(this.value){
                this.show1=true
            } else {
                Toast('请阅读并同意机大呷平台提现协议');
            }
        }
    },
    components:{
        vanNavBar:NavBar,
        vanPopup:Popup,
        vanNoticeBar: NoticeBar,
        vanRadio: Radio,
        vanDialog: Dialog,
        vanRadioGroup: RadioGroup
    }
}
</script>
<style  lang="scss" scoped>
#top{
    width:100%;
    #top1 {
        display:flex;
        justify-content: space-around;
        flex-wrap: wrap;
        margin-bottom: 50px;
        &>span{
            width:100%;
            display: block;
            margin:30px;
            color: #505050;
            font-size: 13pt;
        }
        div{
            display: flex;
            justify-content: space-around;
            align-items: center;
            height:40pt;
            width:70pt;
            // line-height: 1.2rem;
            border:1px #E5E5E5 solid;
            padding:20px;
            // vertical-align:middle;
            // text-align:right;
            // padding-right:20px;
        }
        img{
            width:50px;
            height:50px;
        }
        .active{
            background: url(src/assets/images/选中11@2x.png) no-repeat;
            background-size:98% 100%;
            border:0;
        }
    }
    #top2{
        background: url(src/assets/images/图层18@2x.png) no-repeat 80% center;
        display: flex;
        flex-direction: column;
        .yinhang{
            font-size: 26px;
            margin-left: 30px;
            font-weight: bold;
            color:#444444;
        }
    }
    #top3{
        display:flex;
        flex-direction: column;
        justify-content: space-around;
        flex-wrap: wrap;
        margin-top:30px;
        padding-left: 30px;
        width:100%;
        .tixian{
            width:100%;
            font-size: 13pt;
            font-weight: 600;
            color: #505050;
            i{
                color:red;
                font-style: normal; 
            }
        }
        input{
            margin-top:20px;
            line-height: 50px;
            width:90%;
            font-weight: 500;
            font-size: 26px;
            border-bottom: 1px #EF2C10 solid;
            &::-webkit-input-placeholder{
                color: #A6A6A6;
            }
        }
    }
    button{
       width:75%;
       line-height: 30pt;
       border-radius: 20pt;
       background: #15BDDB;
       font-size: 12pt;
       color:#fff;
    }
    .bottom{
       margin: -30px 60px 60px 60px;
       display: flex;
       flex-direction: row;
       align-items: center;
       font-size: 8pt;
       border-bottom:1px solid rgba(166, 166, 166, 0.4);
       padding-bottom: 10pt;
       span{
           display: inline-block;
           width:30%;
           text-align: right;
       }
       input{
           padding-left:40px;
       }
   }
}
::-webkit-input-placeholder{
    color:#A6A6A6;
}
.height{
    margin:30pt 0 10px 0;
}

/deep/ .van-notice-bar__content{
    font-size: 10pt;
}

/* 协议 */
.xieyi{
  display: flex;
  align-items: center;
  padding-left:13pt;
  margin-top:10pt;
  span{
    color:#15BDDB;
  }
}

/* 协议弹出层 */
.xy_pop{
    position: relative;
    height:100%;
    width:100%;
    background: #fff;
    .top{
        height:85%;
        width:100%;
        padding:20px;
        overflow: scroll;
        p{
            display: block;
            margin-bottom:10px;
        }
    }
    .bottom1{
        position: fixed;
        bottom:10pt;
        left:50%;
        transform: translateX(-50%);
        width:30%;
        text-align: center;
        line-height: 30pt;
        border-radius: 15pt;
        background: #15BDDB;
        color:#fff;
    }
}
/* 确认提交弹出层 */
.submit{
    width:70%;
}
.submit_pop{
    .top{
        padding:20px;
        P{
            text-align: center;
            color:#666;
        }
        .span{
            display: inline-block;
            // margin-right:10px;
            margin: 40px 10px;
            font-size: 36px;
        }
        .code{
            display: flex;
            justify-content: space-around;
        }
        .price{
            color:#FF4C23;
            font-size: 26px;
        }
    }
    .btn{
        display: flex;
        border-top: 1px solid #DEDEDE;
        
        &>div{
            position: relative;
            padding:20px 0;
            text-align: center;
            width:50%;
            color:#15BDDB
        }
        .c9{
            color:#999;
        }
        &>div:nth-child(1)::after{
            position: absolute;
            top:0;
            display: block;
            content:'';
            height:100%;
            width:2px;
            right:0;
            background: #DEDEDE;
        }
        &>div:active{
            background: #ccc;
        }
    }
}
</style>


